<template>
  <div
    v-if="api_data && event && form"
    class="box"
  >
    <h1
      ref="formtitle"
      class="title"
    >
      {{ api_data.title }}<template v-if="event">
        ({{ event.name }})
      </template>
    </h1>
    <hr>
    <div
      v-if="cannotEdit"
      class="flex column"
    >
      <span class="flex row ca subtitle mt-3 mb-6">Não consegue editar este formulário de momento.</span>
      <button
        type="button"
        class="button is-primary"
        @click="back()"
      >
        Voltar
      </button>
    </div>
    <form
      v-else
      id="form-area"
      class="flex column"
      method="post"
      @submit.prevent="submit"
    >
      <ul class="steps has-content-centered mb-0">
        <li
          v-for="(element, index) in api_data.form.sections"
          :key="element.id"
          class="steps-segment is-primary"
          :class="{'is-active' : section == index, 'has-gaps' : section <= index}"
        >
          <span class="steps-marker"><template v-if="!api_data.form.hide_section_numbers">{{ index+1 }}</template></span>
          <div class="steps-content">
            <p :class="{'has-text-weight-bold' : section == index}">
              {{ element.section_name }}
            </p>
          </div>
        </li>
        <li
          class="steps-segment is-primary"
          :class="{'is-active' : section == api_data.form.sections.length}"
        >
          <span class="steps-marker"><template v-if="!api_data.form.hide_section_numbers"> {{ api_data.form.sections.length+1 }} </template></span>
          <div class="steps-content">
            <p>
              Concluída
            </p>
          </div>
        </li>
      </ul>
      <div
        v-if="section < api_data.form.sections.length"
        class="box"
      >
        <div
          v-for="(question, questionIndex) in api_data.form.sections[section].questions"
          :key="question.id"
          :set="(qid = 's' + section + '_q' + questionIndex)"
        >
          <label
            class="label"
            :class="{'required' : question.required}"
            :for="qid"
          >
            <span
              v-if="
                !api_data.form.hide_question_numbers &&
                  !api_data.form.sections[section].hide_question_numbers
              "
            >{{ questionIndex + 1 }}. </span>{{ question.question
            }}</label>
          <component
            :is="question.properties.field_type"
            :question="question"
            :question-index="questionIndex"
            :section-index="section"
            :value="form.content.sections[section][questionIndex].answer"
            :qid="qid"
            @update="updateAnswer"
          />
          <hr
            v-if="questionIndex < api_data.form.sections[section].questions.length-1"
            class="mt-4 mb-4"
            style="width: 100%; height: 1px; background-color: #ddd"
          >
        </div> 
      </div>
      <div
        v-else
        v-html="api_data.submit_message"
      />
      <Submit 
        :error="response && response.id === undefined"
        error-message="Houve um erro na submissão do formulário."
        success-duration="3000"
        :success-message="success"
        :loading="loading"
        :submit-text="section < api_data.form.sections.length-1 ? 'Seguinte' : 'Submeter'"
        :cancel-text="section > 0 ? 'Anterior' : 'Sair'"
        :cancel-confirmation="section == 0 ? 'Deseja sair?&nbsp;<b>A sua resposta não será guardada.</br>' : undefined"
        cancel-conf-button="Sair"
        :hide="section == api_data.form.sections.length"
        @leave="leave()"
        @dismissed="response = undefined; success = undefined"
      />
      <div v-if="false">
        Informação a ser enviada:
        <pre class="mt-1 mb-4"><code>{{ JSON.stringify(form, null, '  ') }}</code></pre>
        <template v-if="response">
          Resposta do servidor: 
          <pre class="mt-1"><code>{{ JSON.stringify(response, null, '  ') }}</code></pre>
          <!-- <pre class="mt-1"><code>{{ response }}</code></pre> -->
          <iframe
            name="result"
            allow="midi; geolocation; microphone; camera; display-capture; encrypted-media;"
            sandbox="allow-modals allow-forms allow-scripts allow-same-origin allow-popups allow-top-navigation-by-user-activation allow-downloads"
            allowfullscreen=""
            allowpaymentrequest=""
            frameborder="0"
            style="width: 100%; height: 50vh;"
            :srcdoc="response"
          />
        </template>
      </div>
    </form>
  </div>
  <div
    v-else
    class="flex column box ca vca"
    style="height: 50vh;"
  >
    <span class="title mb-4">A carregar formulário</span>
    <span
      class="loader mt-4"
      style="width: 100px; height: 100px;"
    />
  </div>
</template>

<script>
import * as fields from "@/fields";
import { API } from "@/http-common";
import { getCookie } from "@/common.js"
import Submit from "@/components/Submit.vue"
export default {
  name: "CreateResponse",
  components : Object.assign({}, {Submit}, fields),
  data() {
    return {
      api_data: undefined,
      form : undefined,
      response: undefined,
      event: undefined,
      section : 0,
      success : undefined,
      loading : false,
      cannotEdit : false
    };
  },
  async created() {
    document.title = "Resposta de Formulário"
    if (!this.$route.query.event) {
      window.location.href = "/forms/notfound";
    }
    await Promise.all([
      API.get("form/".concat(this.$route.params.id))
        .then((response) => this.api_data = response.data).catch(() => window.location.href = "/forms/badrequest"),
      API.get("event/".concat(this.$route.query.event))
        .then((response) => {this.event = response.data}).catch(() => window.location.href = "/forms/badrequest")
      ]
    )
    if (this.api_data.type != 2 && this.event?.formsignupid != this.$route.params.id && this.event?.formfeedbackid != this.$route.params.id)
      window.location.href = "/forms/badrequest";
    await API.get("myFormAnswer/?form=".concat(this.$route.params.id).concat("&event=").concat(this.$route.query.event))
        .then((response) =>
            {
              console.log(response)
              if (response.data[0]) {
                this.form = response.data[0]; 
                if(this.form.status != 3) 
                  this.cannotEdit = true;
              }
              else {
                this.form = {
                  content : { sections : []},
                  form: this.api_data.id,
                  event: this.event.id
                }
              }
            }
        );
    this.initializeData();
  },
  methods: {
    back() {
      if (window.length == 0)
        window.location.href = "/"
      else
        this.$router.back()
    },
    leave() {
      if (this.section > 0) {
        this.section--;
        this.$nextTick(() => this.$refs['formtitle'].scrollIntoView({behavior: 'smooth'}));
      }
      else
        location.href='/forms'
    },
    submit() {
      if (this.section < this.api_data.form.sections.length-1) {
        this.section++;
        this.$nextTick(() => this.$refs['formtitle'].scrollIntoView({behavior: 'smooth'}));
      }
      else {
        this.loading = true;
        var method;
        var url;
        var successMessage;
        if (this.form.id === undefined)  {
          method = "post";
          url = "/myFormAnswer/";
          successMessage = "A resposta foi enviada com sucesso.";
          }
        else {
          method = "put";
          url = "/myFormAnswer/".concat(this.form.id).concat("/");
          successMessage = "A resposta foi editada com sucesso.";
        }
        var options = {
          method : method,
          url : url,
          headers: {
            "X-CSRFToken": getCookie("csrftoken"),
          },
          data: this.form,
          transformResponse: [
            async (data) => {
              this.loading = undefined;
              try {
                this.response = JSON.parse(data);
                this.form = this.response;
                this.success = successMessage;
                this.section++;
                await new Promise(resolve => setTimeout(resolve, this.api_data.redirect_delay*1000));
                location.href = this.api_data.redirect;
              } catch (e) {this.response = data;}
              return this.response;
            },
          ],
        };
        API(options);
      }
    },
    updateAnswer(value) {
      this.form.content.sections[value.sectionIndex][value.questionIndex]["answer"] = value["answer"];
    },
    initializeData() {
      for (var section in this.api_data.form.sections) {
        this.form.content.sections[section] = [];
        for (var question in this.api_data.form.sections[section].questions) {
          this.form.content.sections[section][question] = {
            field_type : this.api_data.form.sections[section].questions[question].properties["field_type"],
          };
        }
      }
    }
  },
};
</script>


<style scoped lang="scss">
  $primary : #3273dc;
  @import "@/styles/bulma-steps.min.scss";

  .steps:not(.is-hollow) .steps-segment.is-active .steps-marker:not(.is-hollow) {
    background-color: $primary;
  }

  .steps:not(.is-hollow) .steps-marker:not(.is-hollow),
  .steps-segment:after {
    background-color:darken($primary, 35%);
  }

  .steps-segment:after {
    box-sizing: content-box;
    margin: 0 !important;
  }
  
</style>

<style lang="scss">
  @import '@/styles/flex-styles.scss';
  @import '@/styles/global-styles.scss';
</style>